<template>
	<div id="statcenter-hot-goods">
		<public-header title="热门商品统计"></public-header>
		<div class="content padding-15 font-14 ">
			<van-row class="c-gray">
				<van-col span="3" class="padding-10-tb">排名</van-col>
				<van-col span="6" class="padding-10-tb">商品名称</van-col>
				<van-col span="6" class="padding-10-tb">店铺名称</van-col>
				<van-col span="5" class="padding-10-tb">销售额</br>占比</van-col>
				<van-col span="4" class="padding-10-tb">销售量</br>占比</van-col>
			</van-row>
			<van-row>
				<van-col span="3">--</van-col>
				<van-col span="6">总计</van-col>
				<van-col span="6">--</van-col>
				<van-col span="5">￥{{total.total_goods_price}}</van-col>
				<van-col span="4">{{total.total_goods_num}}</van-col>
			</van-row>
			<van-row v-for="(item, index) in stat" :key="index" v-if="stat">
				<van-col span="3">{{index + 1}}</van-col>
				<van-col span="6">{{item.goods.title}}</van-col>
				<van-col span="6">{{item.store_name}}</van-col>
				<van-col span="5">￥{{item.total_goods_price + '/' + item.pre_goods_price}}</van-col>
				<van-col span="4">{{item.total_goods_num + '/' + item.pre_goods_num}}</van-col>
			</van-row>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: [],
				total: [],
				store: {},
				orderby: {
					'total_goods_price': '销售额',
					'total_goods_num': '销售量'
				}
			}
		},
		methods: {
			onLoad() {
				let that = this;
				getData({
					vue: this,
					data: {
						detail: 1,
						filter: that.filter ? that.filter.items: {}
					},
					url: 'plateform/statcenter/hot/index',
					success: function(result) {
						that.stat = result.stat;
						that.total = result.total;
						that.store = result.store;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},

	}
</script>

<style scoped>
	#statcenter-hot-goods .van-row{
		font-size: 12px;
		text-align: center;
	}
	#statcenter-hot-goods .van-row:nth-child(odd) {
		background-color: #ddd;
	}
	#statcenter-hot-goods .van-row .van-col{
		padding: 8px 0;
		border-right: 1px solid #eee;
	}
	#statcenter-hot-goods .van-row .van-col:last-child{
		border-right: 0px;
	}
</style>


